const categories = [
  { id: 'all', name: 'Todos', icon: SparklesIcon, color: 'neon-pink', emoji: '✨' },
  { id: 'hair', name: 'Cabello', icon: SparklesIcon, color: 'beauty-coral', emoji: '💇‍♀️' },
  { id: 'nails', name: 'Uñas', icon: SparklesIcon, color: 'beauty-mint', emoji: '💅' },
  { id: 'makeup', name: 'Maquillaje', icon: SparklesIcon, color: 'beauty-lavender', emoji: '💄' },
  { id: 'skincare', name: 'Skincare', icon: SparklesIcon, color: 'beauty-peach', emoji: '🌸' },
]

export default function ServicesPage() {
  const { t } = useTranslation()
  const [services, setServices] = useState(mockServices)
  const [currentServiceIndex, setCurrentServiceIndex] = useState(0)
  const [selectedCategory, setSelectedCategory] = useState('all')
  const [searchQuery, setSearchQuery] = useState('')
  const [likedServices, setLikedServices] = useState<string[]>([])
  const [isFilterOpen, setIsFilterOpen] = useState(false)
  const [viewMode, setViewMode] = useState<'swipe' | 'grid'>('swipe')
  const [isMobile, setIsMobile] = useState(true)

  const filteredServices = services.filter(service => {
    const matchesCategory = selectedCategory === 'all' || service.category === selectedCategory
    const matchesSearch = service.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
                         service.tags.some(tag => tag.toLowerCase().includes(searchQuery.toLowerCase()))
    return matchesCategory && matchesSearch
  })

  const currentService = filteredServices[currentServiceIndex]

  const handleSwipeLeft = () => {
    if (currentServiceIndex < filteredServices.length - 1) {
      setCurrentServiceIndex(prev => prev + 1)
    }
  }

  const handleSwipeRight = () => {
    if (currentService) {
      setLikedServices(prev => [...prev, currentService.id])
      if (currentServiceIndex < filteredServices.length - 1) {
        setCurrentServiceIndex(prev => prev + 1)
      }
    }
  }

  const handleServiceTap = () => {
    // Navigate to service detail page
    console.log('Navigate to service:', currentService?.id)
  }

  const handleLikeService = (serviceId: string) => {
    setLikedServices(prev =>
      prev.includes(serviceId)
        ? prev.filter(id => id !== serviceId)
        : [...prev, serviceId]
    )
  }

  const handleBookService = (serviceId: string) => {
    console.log('Book service:', serviceId)
    // Navigate to booking page
  }

  // Responsive detection
  useEffect(() => {
    const checkScreenSize = () => {
      const mobile = window.innerWidth < 768
      setIsMobile(mobile)
      setViewMode(mobile ? 'swipe' : 'grid')
    }

    checkScreenSize()
    window.addEventListener('resize', checkScreenSize)
    return () => window.removeEventListener('resize', checkScreenSize)
  }, [])

  useEffect(() => {
    setCurrentServiceIndex(0)
  }, [selectedCategory, searchQuery])

  return (
    <div className="min-h-screen bg-gradient-to-br from-beauty-blush/20 via-white to-beauty-lavender/20">
      {/* Header - Sticky for desktop */}
      <div className={`${!isMobile ? 'sticky top-0 z-40' : ''} bg-white/90 backdrop-blur-xl border-b border-beauty-blush/20`}>
        <div className="container-responsive py-6">
          <motion.div
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            className="text-center mb-6"
          >
            <h1 className="text-3xl md:text-4xl font-bold bg-gradient-cotton-candy bg-clip-text text-transparent mb-2">
              {t('services.title')} ✨
            </h1>
            <p className="text-gray-600">
              {isMobile ? 'Desliza para descubrir servicios increíbles' : 'Descubre servicios de belleza increíbles'}
            </p>
          </motion.div>

        {/* Search and Filter */}
        <div className="flex space-x-3">
          <div className="flex-1 relative">
            <MagnifyingGlassIcon className="absolute left-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-white/60" />
            <input
              type="text"
              placeholder="Buscar servicios..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="w-full pl-10 pr-4 py-3 genz-card text-white placeholder-white/60 border border-white/20 rounded-xl focus:ring-2 focus:ring-neon-pink focus:border-transparent"
            />
          </div>
          <motion.button
            whileTap={{ scale: 0.95 }}
            onClick={() => setIsFilterOpen(!isFilterOpen)}
            className="p-3 genz-card border border-white/20 rounded-xl text-white hover:shadow-neon transition-all duration-300"
          >
            <AdjustmentsHorizontalIcon className="h-6 w-6" />
          </motion.button>
        </div>

        {/* Category Filter */}
        <AnimatePresence>
          {isFilterOpen && (
            <motion.div
              initial={{ height: 0, opacity: 0 }}
              animate={{ height: 'auto', opacity: 1 }}
              exit={{ height: 0, opacity: 0 }}
              className="overflow-hidden"
            >
              <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
                {categories.map((category) => (
                  <motion.button
                    key={category.id}
                    whileTap={{ scale: 0.95 }}
                    onClick={() => setSelectedCategory(category.id)}
                    className={`p-3 rounded-xl border transition-all duration-300 ${
                      selectedCategory === category.id
                        ? 'bg-gradient-tiktok border-neon-pink text-white shadow-neon-pink'
                        : 'genz-card border-white/20 text-white hover:border-beauty-cyber'
                    }`}
                  >
                    <category.icon className="h-5 w-5 mx-auto mb-1" />
                    <span className="text-sm font-medium">{category.name}</span>
                  </motion.button>
                ))}
              </div>
            </motion.div>
          )}
        </AnimatePresence>

        </div>
      </div>

      {/* Main Content */}
      <div className="container-responsive py-8">
        {/* Mobile Swipe Interface */}
        {isMobile && (
          <div className="space-y-6">
            {/* Service Card */}
            <div className="flex justify-center items-center min-h-[400px]">
              {currentService ? (
                <SwipeableCard
                  item={currentService}
                  onSwipeLeft={handleSwipeLeft}
                  onSwipeRight={handleSwipeRight}
                  onTap={handleServiceTap}
                />
              ) : (
                <div className="text-center space-y-4">
                  <div className="text-6xl">🎉</div>
                  <h3 className="text-xl font-bold bg-gradient-cotton-candy bg-clip-text text-transparent">
                    ¡Has visto todos los servicios!
                  </h3>
                  <p className="text-gray-600">
                    {likedServices.length > 0
                      ? `Te gustaron ${likedServices.length} servicios`
                      : 'Prueba con diferentes filtros'
                    }
                  </p>
                  <motion.button
                    whileTap={{ scale: 0.95 }}
                    onClick={() => {
                      setCurrentServiceIndex(0)
                      setSelectedCategory('all')
                      setSearchQuery('')
                    }}
                    className="px-6 py-3 bg-gradient-tiktok text-white rounded-2xl font-medium hover:scale-105 transition-transform duration-300 shadow-neon-pink"
                  >
                    Empezar de nuevo
                  </motion.button>
                </div>
              )}
            </div>

            {/* Progress Indicator */}
            {filteredServices.length > 0 && (
              <div className="flex justify-center space-x-2">
                {filteredServices.map((_, index) => (
                  <div
                    key={index}
                    className={`h-2 rounded-full transition-all duration-300 ${
                      index === currentServiceIndex
                        ? 'w-8 bg-neon-pink'
                        : index < currentServiceIndex
                        ? 'w-2 bg-beauty-cyber'
                        : 'w-2 bg-white/30'
                    }`}
                  />
                ))}
              </div>
            )}
          </div>
        )}

        {/* Desktop Grid Interface */}
        {!isMobile && (
          <div className="space-y-8">
            {/* Services Grid */}
            <AnimatePresence mode="wait">
              <motion.div
                key={selectedCategory + searchQuery}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: -20 }}
                transition={{ duration: 0.4 }}
                className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6"
              >
                {filteredServices.length > 0 ? (
                  filteredServices.map((service, index) => (
                    <motion.div
                      key={service.id}
                      initial={{ opacity: 0, y: 30 }}
                      animate={{ opacity: 1, y: 0 }}
                      transition={{ delay: index * 0.1, duration: 0.5 }}
                    >
                      <ServiceCard
                        service={service}
                        onLike={handleLikeService}
                        onBook={handleBookService}
                        isLiked={likedServices.includes(service.id)}
                        className="h-full"
                      />
                    </motion.div>
                  ))
                ) : (
                  <div className="col-span-full text-center py-16 space-y-6">
                    <motion.div
                      initial={{ scale: 0 }}
                      animate={{ scale: 1 }}
                      transition={{ delay: 0.2, type: 'spring' }}
                      className="text-8xl"
                    >
                      🔍
                    </motion.div>
                    <div className="space-y-4">
                      <h3 className="text-2xl font-bold bg-gradient-cotton-candy bg-clip-text text-transparent">
                        No se encontraron servicios
                      </h3>
                      <p className="text-gray-600 max-w-md mx-auto">
                        No encontramos servicios que coincidan con tu búsqueda. Prueba con otros filtros o términos.
                      </p>
                      <motion.button
                        whileTap={{ scale: 0.95 }}
                        onClick={() => {
                          setSelectedCategory('all')
                          setSearchQuery('')
                        }}
                        className="px-6 py-3 bg-gradient-tiktok text-white rounded-2xl font-medium hover:scale-105 transition-transform duration-300 shadow-neon-pink"
                      >
                        Ver todos los servicios
                      </motion.button>
                    </div>
                  </div>
                )}
              </motion.div>
            </AnimatePresence>

            {/* Services Summary */}
            {filteredServices.length > 0 && (
              <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: 0.3 }}
                className="text-center"
              >
                <p className="text-gray-600">
                  Mostrando {filteredServices.length} servicio{filteredServices.length !== 1 ? 's' : ''}
                  {selectedCategory !== 'all' && ` en ${categories.find(c => c.id === selectedCategory)?.name}`}
                </p>
              </motion.div>
            )}
          </div>
        )}

      </div>

      {/* Liked Services Count */}
      {likedServices.length > 0 && (
        <motion.div
          initial={{ scale: 0 }}
          animate={{ scale: 1 }}
          className="fixed top-20 right-4 bg-gradient-tiktok text-white px-4 py-2 rounded-full shadow-neon-pink z-20"
        >
          ❤️ {likedServices.length}
        </motion.div>
      )}
    </div>
  )
}