import { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { motion, AnimatePresence } from 'framer-motion'
import { useForm } from 'react-hook-form'
import {
  SparklesIcon,
  ArrowRightIcon,
  ArrowLeftIcon,
  CheckCircleIcon,
  PhotoIcon,
  XMarkIcon
} from '@heroicons/react/24/outline'
import { authService } from '@/services/authService'
import type { StylistRegisterForm } from '@/types'
import toast from 'react-hot-toast'

const steps = [
  { id: 1, name: 'Información Personal', description: 'Datos básicos y de contacto' },
  { id: 2, name: 'Información Profesional', description: 'Experiencia y especialidades' },
  { id: 3, name: 'Ubicación y Contacto', description: 'Dirección y redes sociales' },
  { id: 4, name: 'Portfolio', description: 'Imágenes de tu trabajo' },
  { id: 5, name: 'Finalizar', description: 'Revisión y confirmación' },
]

const specialtyOptions = [
  'Corte de Cabello',
  'Coloración',
  'Peinados',
  'Tratamientos Capilares',
  'Maquillaje',
  'Cejas y Pestañas',
  'Manicure',
  'Pedicure',
  'Tratamientos Faciales',
  'Depilación',
  'Masajes',
  'Extensiones'
]

export default function StylistRegisterPage() {
  const [currentStep, setCurrentStep] = useState(1)
  const [isLoading, setIsLoading] = useState(false)
  const [portfolioFiles, setPortfolioFiles] = useState<File[]>([])
  const [portfolioPreviews, setPortfolioPreviews] = useState<string[]>([])
  const navigate = useNavigate()

  const {
    register,
    handleSubmit,
    watch,
    setValue,
    getValues,
    formState: { errors },
  } = useForm<StylistRegisterForm>()

  const watchedFields = watch()

  const handleFileUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const files = Array.from(event.target.files || [])
    if (files.length + portfolioFiles.length > 6) {
      toast.error('Máximo 6 imágenes permitidas')
      return
    }

    setPortfolioFiles(prev => [...prev, ...files])

    // Create previews
    files.forEach(file => {
      const reader = new FileReader()
      reader.onload = (e) => {
        if (e.target?.result) {
          setPortfolioPreviews(prev => [...prev, e.target!.result as string])
        }
      }
      reader.readAsDataURL(file)
    })
  }

  const removePortfolioImage = (index: number) => {
    setPortfolioFiles(prev => prev.filter((_, i) => i !== index))
    setPortfolioPreviews(prev => prev.filter((_, i) => i !== index))
  }

  const handleSpecialtyToggle = (specialty: string) => {
    const currentSpecialties = getValues('specialties') || []
    const newSpecialties = currentSpecialties.includes(specialty)
      ? currentSpecialties.filter(s => s !== specialty)
      : [...currentSpecialties, specialty]

    setValue('specialties', newSpecialties)
  }

  const nextStep = () => {
    if (currentStep < steps.length) {
      setCurrentStep(currentStep + 1)
    }
  }

  const prevStep = () => {
    if (currentStep > 1) {
      setCurrentStep(currentStep - 1)
    }
  }

  const onSubmit = async (data: StylistRegisterForm) => {
    setIsLoading(true)

    try {
      // Create FormData for file upload
      const formData = new FormData()

      // Add text fields
      Object.entries(data).forEach(([key, value]) => {
        if (key !== 'portfolioImages' && value !== undefined) {
          if (Array.isArray(value)) {
            formData.append(key, JSON.stringify(value))
          } else {
            formData.append(key, value.toString())
          }
        }
      })

      // Add portfolio images
      portfolioFiles.forEach((file, index) => {
        formData.append(`portfolioImages`, file)
      })

      const response = await authService.registerStylist(formData)

      if (response.success) {
        toast.success('¡Registro exitoso! Te contactaremos pronto para la verificación.')
        navigate('/login', {
          state: { message: 'Registro de estilista completado. Te contactaremos para la verificación.' }
        })
      } else {
        throw new Error(response.error || 'Error en el registro')
      }
    } catch (error: any) {
      toast.error(error.message || 'Error al registrar estilista')
    } finally {
      setIsLoading(false)
    }
  }

  const renderStep = () => {
    switch (currentStep) {
      case 1:
        return (
          <div className="space-y-6">
            <div className="grid grid-cols-2 gap-4">
              <div className="form-group">
                <label className="label">Nombre *</label>
                <input
                  {...register('firstName', { required: 'El nombre es requerido' })}
                  className={`input ${errors.firstName ? 'input-error' : ''}`}
                  placeholder="Juan"
                />
                {errors.firstName && <p className="form-error">{errors.firstName.message}</p>}
              </div>

              <div className="form-group">
                <label className="label">Apellido *</label>
                <input
                  {...register('lastName', { required: 'El apellido es requerido' })}
                  className={`input ${errors.lastName ? 'input-error' : ''}`}
                  placeholder="Pérez"
                />
                {errors.lastName && <p className="form-error">{errors.lastName.message}</p>}
              </div>
            </div>

            <div className="form-group">
              <label className="label">Email *</label>
              <input
                {...register('email', {
                  required: 'El email es requerido',
                  pattern: { value: /^\S+@\S+$/i, message: 'Email inválido' }
                })}
                type="email"
                className={`input ${errors.email ? 'input-error' : ''}`}
                placeholder="tu@email.com"
              />
              {errors.email && <p className="form-error">{errors.email.message}</p>}
            </div>

            <div className="form-group">
              <label className="label">Teléfono *</label>
              <input
                {...register('phone', { required: 'El teléfono es requerido' })}
                type="tel"
                className={`input ${errors.phone ? 'input-error' : ''}`}
                placeholder="+52 55 1234 5678"
              />
              {errors.phone && <p className="form-error">{errors.phone.message}</p>}
            </div>

            <div className="grid grid-cols-2 gap-4">
              <div className="form-group">
                <label className="label">Contraseña *</label>
                <input
                  {...register('password', {
                    required: 'La contraseña es requerida',
                    minLength: { value: 8, message: 'Mínimo 8 caracteres' }
                  })}
                  type="password"
                  className={`input ${errors.password ? 'input-error' : ''}`}
                  placeholder="••••••••"
                />
                {errors.password && <p className="form-error">{errors.password.message}</p>}
              </div>

              <div className="form-group">
                <label className="label">Confirmar Contraseña *</label>
                <input
                  {...register('confirmPassword', {
                    required: 'Confirma tu contraseña',
                    validate: value => value === watchedFields.password || 'Las contraseñas no coinciden'
                  })}
                  type="password"
                  className={`input ${errors.confirmPassword ? 'input-error' : ''}`}
                  placeholder="••••••••"
                />
                {errors.confirmPassword && <p className="form-error">{errors.confirmPassword.message}</p>}
              </div>
            </div>
          </div>
        )

      case 2:
        return (
          <div className="space-y-6">
            <div className="form-group">
              <label className="label">Nombre del Negocio *</label>
              <input
                {...register('businessName', { required: 'El nombre del negocio es requerido' })}
                className={`input ${errors.businessName ? 'input-error' : ''}`}
                placeholder="Salón de Belleza María"
              />
              {errors.businessName && <p className="form-error">{errors.businessName.message}</p>}
            </div>

            <div className="form-group">
              <label className="label">Años de Experiencia *</label>
              <select
                {...register('experience', { required: 'La experiencia es requerida' })}
                className={`input ${errors.experience ? 'input-error' : ''}`}
              >
                <option value="">Selecciona...</option>
                <option value="1">1 año</option>
                <option value="2">2 años</option>
                <option value="3">3 años</option>
                <option value="4">4 años</option>
                <option value="5">5+ años</option>
                <option value="10">10+ años</option>
              </select>
              {errors.experience && <p className="form-error">{errors.experience.message}</p>}
            </div>

            <div className="form-group">
              <label className="label">Especialidades *</label>
              <p className="text-sm text-gray-600 mb-3">Selecciona tus áreas de especialización</p>
              <div className="grid grid-cols-2 gap-2">
                {specialtyOptions.map(specialty => (
                  <label key={specialty} className="flex items-center space-x-2">
                    <input
                      type="checkbox"
                      checked={watchedFields.specialties?.includes(specialty) || false}
                      onChange={() => handleSpecialtyToggle(specialty)}
                      className="rounded border-gray-300 text-primary-600 focus:ring-primary-500"
                    />
                    <span className="text-sm">{specialty}</span>
                  </label>
                ))}
              </div>
              {errors.specialties && <p className="form-error">{errors.specialties.message}</p>}
            </div>

            <div className="form-group">
              <label className="label">Biografía</label>
              <textarea
                {...register('bio')}
                rows={4}
                className="input"
                placeholder="Cuéntanos sobre ti, tu experiencia y lo que te apasiona de la belleza..."
              />
            </div>
          </div>
        )

      case 3:
        return (
          <div className="space-y-6">
            <div className="grid grid-cols-2 gap-4">
              <div className="form-group">
                <label className="label">Ciudad *</label>
                <input
                  {...register('locationCity', { required: 'La ciudad es requerida' })}
                  className={`input ${errors.locationCity ? 'input-error' : ''}`}
                  placeholder="Ciudad de México"
                />
                {errors.locationCity && <p className="form-error">{errors.locationCity.message}</p>}
              </div>

              <div className="form-group">
                <label className="label">Estado *</label>
                <input
                  {...register('locationState', { required: 'El estado es requerido' })}
                  className={`input ${errors.locationState ? 'input-error' : ''}`}
                  placeholder="CDMX"
                />
                {errors.locationState && <p className="form-error">{errors.locationState.message}</p>}
              </div>
            </div>

            <div className="form-group">
              <label className="label">Instagram (opcional)</label>
              <input
                {...register('instagramUrl')}
                className="input"
                placeholder="@tuinstagram o https://instagram.com/tuinstagram"
              />
            </div>
          </div>
        )

      case 4:
        return (
          <div className="space-y-6">
            <div className="text-center">
              <PhotoIcon className="h-12 w-12 text-gray-400 mx-auto mb-4" />
              <h3 className="text-lg font-semibold text-gray-900 mb-2">
                Muestra tu Trabajo
              </h3>
              <p className="text-gray-600 mb-6">
                Sube hasta 6 imágenes de tu mejor trabajo (máximo 5MB cada una)
              </p>
            </div>

            <div className="border-2 border-dashed border-gray-300 rounded-lg p-6">
              <input
                type="file"
                multiple
                accept="image/*"
                onChange={handleFileUpload}
                className="hidden"
                id="portfolio-upload"
              />
              <label
                htmlFor="portfolio-upload"
                className="cursor-pointer flex flex-col items-center"
              >
                <PhotoIcon className="h-8 w-8 text-gray-400 mb-2" />
                <span className="text-sm font-medium text-gray-700">
                  Haz clic para seleccionar imágenes
                </span>
                <span className="text-xs text-gray-500">
                  PNG, JPG hasta 5MB cada una
                </span>
              </label>
            </div>

            {portfolioPreviews.length > 0 && (
              <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
                {portfolioPreviews.map((preview, index) => (
                  <div key={index} className="relative group">
                    <img
                      src={preview}
                      alt={`Portfolio ${index + 1}`}
                      className="w-full h-32 object-cover rounded-lg"
                    />
                    <button
                      type="button"
                      onClick={() => removePortfolioImage(index)}
                      className="absolute -top-2 -right-2 bg-red-500 text-white rounded-full p-1 opacity-0 group-hover:opacity-100 transition-opacity"
                    >
                      <XMarkIcon className="h-4 w-4" />
                    </button>
                  </div>
                ))}
              </div>
            )}
          </div>
        )

      case 5:
        return (
          <div className="space-y-6">
            <div className="text-center mb-8">
              <CheckCircleIcon className="h-12 w-12 text-green-500 mx-auto mb-4" />
              <h3 className="text-lg font-semibold text-gray-900 mb-2">
                ¡Casi Terminamos!
              </h3>
              <p className="text-gray-600">
                Revisa tu información antes de enviar tu solicitud
              </p>
            </div>

            <div className="bg-gray-50 rounded-lg p-6 space-y-4">
              <div>
                <h4 className="font-semibold text-gray-900">Información Personal</h4>
                <p className="text-sm text-gray-600">
                  {watchedFields.firstName} {watchedFields.lastName}
                </p>
                <p className="text-sm text-gray-600">{watchedFields.email}</p>
                <p className="text-sm text-gray-600">{watchedFields.phone}</p>
              </div>

              <div>
                <h4 className="font-semibold text-gray-900">Información Profesional</h4>
                <p className="text-sm text-gray-600">{watchedFields.businessName}</p>
                <p className="text-sm text-gray-600">{watchedFields.experience} años de experiencia</p>
                <p className="text-sm text-gray-600">
                  {watchedFields.specialties?.join(', ')}
                </p>
              </div>

              <div>
                <h4 className="font-semibold text-gray-900">Ubicación</h4>
                <p className="text-sm text-gray-600">
                  {watchedFields.locationCity}, {watchedFields.locationState}
                </p>
              </div>

              <div>
                <h4 className="font-semibold text-gray-900">Portfolio</h4>
                <p className="text-sm text-gray-600">
                  {portfolioFiles.length} imágenes seleccionadas
                </p>
              </div>
            </div>

            <div className="flex items-start space-x-3">
              <input
                {...register('acceptTerms', { required: 'Debes aceptar los términos' })}
                id="acceptTerms"
                type="checkbox"
                className="h-4 w-4 text-primary-600 focus:ring-primary-500 border-gray-300 rounded mt-1"
              />
              <div className="text-sm">
                <label htmlFor="acceptTerms" className="text-gray-700">
                  Acepto los términos y condiciones para estilistas de BeautyCita
                </label>
                {errors.acceptTerms && (
                  <p className="form-error mt-1">{errors.acceptTerms.message}</p>
                )}
              </div>
            </div>
          </div>
        )

      default:
        return null
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-primary-50 to-secondary-50 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-2xl mx-auto">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          className="text-center mb-8"
        >
          <Link to="/" className="inline-flex items-center space-x-2 mb-6">
            <SparklesIcon className="h-10 w-10 text-primary-600" />
            <span className="text-2xl font-serif font-bold gradient-text">
              BeautyCita
            </span>
          </Link>

          <h1 className="text-3xl font-serif font-bold text-gray-900 mb-2">
            Únete como Estilista
          </h1>
          <p className="text-gray-600">
            Comparte tu talento con miles de clientes
          </p>
        </motion.div>

        {/* Progress Steps */}
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.6, delay: 0.1 }}
          className="mb-8"
        >
          <div className="flex items-center justify-between">
            {steps.map((step, index) => (
              <div key={step.id} className="flex items-center">
                <div
                  className={`flex items-center justify-center w-8 h-8 rounded-full border-2 ${
                    step.id <= currentStep
                      ? 'bg-primary-600 border-primary-600 text-white'
                      : 'border-gray-300 text-gray-400'
                  }`}
                >
                  {step.id < currentStep ? (
                    <CheckCircleIcon className="h-5 w-5" />
                  ) : (
                    <span className="text-sm font-medium">{step.id}</span>
                  )}
                </div>
                {index < steps.length - 1 && (
                  <div
                    className={`w-full h-1 mx-2 ${
                      step.id < currentStep ? 'bg-primary-600' : 'bg-gray-300'
                    }`}
                  />
                )}
              </div>
            ))}
          </div>

          <div className="mt-4">
            <h2 className="text-lg font-semibold text-gray-900">
              {steps[currentStep - 1].name}
            </h2>
            <p className="text-sm text-gray-600">
              {steps[currentStep - 1].description}
            </p>
          </div>
        </motion.div>

        {/* Form */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.2 }}
          className="card"
        >
          <div className="card-body">
            <form onSubmit={handleSubmit(onSubmit)}>
              <AnimatePresence mode="wait">
                <motion.div
                  key={currentStep}
                  initial={{ opacity: 0, x: 20 }}
                  animate={{ opacity: 1, x: 0 }}
                  exit={{ opacity: 0, x: -20 }}
                  transition={{ duration: 0.3 }}
                >
                  {renderStep()}
                </motion.div>
              </AnimatePresence>

              {/* Navigation */}
              <div className="flex justify-between mt-8 pt-6 border-t border-gray-200">
                <button
                  type="button"
                  onClick={prevStep}
                  disabled={currentStep === 1}
                  className={`btn ${
                    currentStep === 1
                      ? 'btn-outline opacity-50 cursor-not-allowed'
                      : 'btn-outline'
                  } flex items-center space-x-2`}
                >
                  <ArrowLeftIcon className="h-4 w-4" />
                  <span>Anterior</span>
                </button>

                {currentStep < steps.length ? (
                  <button
                    type="button"
                    onClick={nextStep}
                    className="btn btn-primary flex items-center space-x-2"
                  >
                    <span>Siguiente</span>
                    <ArrowRightIcon className="h-4 w-4" />
                  </button>
                ) : (
                  <button
                    type="submit"
                    disabled={isLoading}
                    className="btn btn-primary flex items-center space-x-2"
                  >
                    {isLoading ? (
                      <div className="loading-spinner" />
                    ) : (
                      <>
                        <span>Enviar Solicitud</span>
                        <CheckCircleIcon className="h-4 w-4" />
                      </>
                    )}
                  </button>
                )}
              </div>
            </form>
          </div>
        </motion.div>

        {/* Login link */}
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.6, delay: 0.3 }}
          className="text-center mt-6"
        >
          <p className="text-sm text-gray-600">
            ¿Ya tienes una cuenta?{' '}
            <Link
              to="/login"
              className="font-medium text-primary-600 hover:text-primary-700"
            >
              Inicia sesión aquí
            </Link>
          </p>
        </motion.div>
      </div>
    </div>
  )
}